body {
    background-color: rgb(39, 39, 39);
    color: whitesmoke;
}

.jumbotron {
    background-color: rgb(104, 104, 104);
    color: whitesmoke;
    height: 200px;
    width: 1048px;
}

.jumboText {
    text-align: center;
    background-color: rgb(102, 102, 102);
    color: whitesmoke;
}

.score {
    color: red;
}
.navbar {
    background-color: rgb(49, 49, 49);
    position: fixed;
    top: 0;
    width: 1048px;
    z-index: 1;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  width: 1048px;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 150px;
  margin: 1rem;
  position: relative;
  width: 200px;
}

.card > .img-container {
  width: 200px;
  height: 200px;
  overflow: hidden;
  text-align: center;
}
.card > .img-container > img {
  width: 90%;
}

